$primary : #e67e22;
$font-folor : #959595;
@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

html {

    body {
        margin: 0;
        padding: 0;
    }

    

    a {
        text-decoration: none;
        color: #959595;
        
        
    }


    header {
        background-color: $primary;
        font-family: 'Lato', sans-serif;
        margin: 0;
        padding: 0;
        height: 100vh;
        
        
        .nav {
            background: rgba(#2c3e50, 1);
            border-bottom: 1px solid yellow;
            display: grid;
            grid-template-columns: 1fr 3fr 1fr;
            height: 10vh;
            align-items: center;
            overflow: hidden;
            
            
            a {
                margin: 0 10px;
                transition: .5s color;
                
               }

            a:hover {
                color: white;
            }
    
            & > .nav__logo {
                grid-column: 1/2;
                justify-self: center;
            }

            color: #959595;
            
    
           & > .nav__center {
               justify-self: center;
           }

           & > .nav__right {
               color: #959595;
               justify-self: start;
               a {
                   margin: auto 15px;
               }
               .fa {
                   font-size: 150%;

               }
           }
        }
    
        .main {
            background: transparent;
        
            & > .wrapper {
                width: 70%;
                margin: 0 auto;
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;
                grid-template-rows: 150px;
                margin-top: 100px;
                grid-gap: 10px;

                & > .box {
                    justify-self: center;
                    align-self: center;
                    text-align: center;
                    width: 80%;
                    color: white;
                    margin: 0 auto;


                    .fa {
                        font-size: 3rem;
                    }


                }
                

               }
    
            }
            
        }

    }

